<template>
	<el-table
			:data="tableData"
			stripe
			style="width: 100%">
		<el-table-column
				prop="id"
				label="#ID"
				width="80">
		</el-table-column>
		<el-table-column
				prop="username"
				label="用户名"
				width="180">
		</el-table-column>
		<el-table-column
				prop="portrait"
				label="头像">
		</el-table-column>
		<el-table-column
				prop="create_at"
				label="创建时间">
		</el-table-column>
	</el-table>
</template>

<script>
  export default {
    data() {
      return {
        tableData: []
      }
    },
		methods: {
      loadList() {
        this.$httpGet("admin/userList", this.form).then((response) => {
          if (response.code === 0) {
            this.tableData = response.data.list
          }
        }).catch(() => {
          this.$message({
            message: '请求服务器异常, 请稍后再试',
            center: true,
            type: "error"
          });
        })
      },
		},
		created() {
      this.loadList()
    }
  }
</script>

